import React, { Component, PropTypes } from 'react';
import { Router, Route, Link ,hashHistory} from 'react-router';
import LazyLoad from '../../src/index';
import Widget from '../../src/Widget';
import _ from 'underscore';
import classNames from 'classnames';
//import...from....
import Title from '../../module/navTitle/navTitle';             //
import ProjectList from '../../module/projectList/projectList';//
import {Result , RefreshControl, Picker, ListView , SwipeAction , Progress , Steps,NoticeBar, Icon, Card, WingBlank, WhiteSpace  , Carousel , Accordion, List ,Grid ,Tag , ActivityIndicator ,Popup, Button, InputItem} from 'antd-mobile';          //
import Modal from '../../module/modal/modal'                      //

// start
//scss import...
import '../../../scss/base/common.scss';
import './invest.scss';
// end
//class ... extends Component{...}
var _that=null;
class Invest extends Component {
    constructor(){
        super();
        this.state={
            text:'',
            val1:'',
            val2:'',
            val3:'',
            text:"----",
            data: [],
            cols: 1,
            pickerValue: [],
            asyncValue: [],
            incomeData: [],
            incomePickerValue: [],
            incomeAsyncValue: [],
            timeData:[],
            timePickerValue: [],
            timeAsyncValue: [],

        }
    };
    componentWillMount(){
        _that = this;
    }
    componentDidMount(){
        //  start
        componentStore.set(this);
        //  end
    };
    componentWillUnmount(){
        //  start
        componentStore.clear(this);
        //  end
    };
    handClick(){
        this.setState({
            btnSt:true
        })
    }
    //
    homePage(){
        Utils.switchRoute('/home')
    }
    investPage(){
        Utils.switchRoute('/invest')
    }
    myPage(){
       Utils.switchRoute('/my')
    }
    //
    //
    changeValues(val1,val2,val3){
        var valStr='';
        if(val1==''&& val2==''&& val3==''){
            valStr=''
        }else if(val1==''&& val2==''&& val3 !=''){
            valStr="3-----"+val3
        }else if(val1==''&& val2 !=''&& val3 !=''){
            valStr="3--"+val2+"--"+val3
        }else if(val1 !=''&& val2 !=''&& val3 !=''){
            valStr=val1+"---"+val2+"---"+val3;
        }else if(val1 !=''&& val2 ==''&& val3 !=''){
            valStr=val1+"------"+val3
        }else if(val1 !=''&& val2 ==''&& val3 ==''){
            valStr=val1+"----"
        }else if(val1 !=''&& val2 !=''&& val3 ==''){
            valStr=val1+"--"+val2+"--"
        }else if(val1==''&& val2 !=''&& val3==''){
            valStr="3--"+val2+"--"
        }
        this.setState({
            text:valStr
        })
    }
    //
    onPickerChange (val) {//change
        //const str="---";
        componentStore.update(_that,{
            bankValue:val //
        })
        let colNum = 1;
        const bank = [...this.state.data];
        const asyncValue = [...val];
        this.setState({
            data: bank,
            cols: colNum,
            asyncValue,
        });
        console.log(this.state.bankValue[0]);
        this.setState({
            val1:this.state.bankValue[0]
        })
        this.changeValues(this.state.bankValue[0],this.state.val2,this.state.val3);
    };
    onClick (){
        var _this = this;
        Utils.requestData({
            url: config.mock_baseUrl + 'api/module1/test_search2',
            method: 'POST',
            data: {
                a: 1,
                b: 2
            },
            callback: function(data){
                console.log("data--:"+ JSON.stringify(data.dataList.dataA));
                componentStore.update(_this,{
                    data: data.dataList.dataA
                })

                //console.log(data)
            }
        });
    };
    //
    incomeOnPickerChange(val) {//change
        //console.log(val);
        componentStore.update(_that,{
            bankValue:val //
        })
        let colNum = 1;
        const bank = [...this.state.incomeData];
        const incomeAsyncValue = [...val];
        this.setState({
            incomeData: bank,
            cols: colNum,
            incomeAsyncValue,
        });
        console.log(this.state.bankValue[0]);
        this.setState({
            val2:this.state.bankValue[0]
        })
        this.changeValues(this.state.val1,this.state.bankValue[0],this.state.val3);
    };
    incomeOnClick (){
        var _this = this;
        Utils.requestData({
            url: config.mock_baseUrl + 'api/module1/test_search2',
            method: 'POST',
            data: {
                a: 1,
                b: 2
            },
            callback: function(data){
                console.log("data--:"+ JSON.stringify(data.dataList.dataB));
                componentStore.update(_this,{
                    incomeData: data.dataList.dataB
                })

                //console.log(data)
            }
        });
    };
    //
    timeOnPickerChange(val) {//change
        //console.log(val);
        componentStore.update(_that,{
            bankValue:val //
        })
        let colNum = 1;
        const bank = [...this.state.timeData];
        const timeAsyncValue = [...val];
        this.setState({
            timeData: bank,
            cols: colNum,
            timeAsyncValue,
        });
        console.log(this.state.bankValue[0]);
        this.setState({
            val3:this.state.bankValue[0]
        })
        this.changeValues(this.state.val1,this.state.val2,this.state.bankValue[0]);
    };
    timeOnClick (){
        var _this = this;
        Utils.requestData({
            url: config.mock_baseUrl + 'api/module1/test_search2',
            method: 'POST',
            data: {
                a: 1,
                b: 2
            },
            callback: function(data){
                console.log("data--:"+ JSON.stringify(data.dataList.dataC));
                componentStore.update(_this,{
                    timeData: data.dataList.dataC
                })

                //console.log(data)
            }
        });
    };
    render(){
        const hProp = this.state.initialHeight ? { height: this.state.initialHeight } : {};
        var settings = {
            dots: true,
            infinite: true,
            speed: 500,
            slidesToShow: 1,
            slidesToScroll: 1
        };
        /*console.log("btnSt----:"+this.state.btnSt);*/

        return (
            <div>
                <div className="home clearfix">
                    <Title
                        title={""}
                        $id="navTitle"
                        munIng={require("../../../image/mbtn.png")}
                        addr={""}
                        citys={this.cityList}
                        grade="1"
                        grade_child="2"
                        toInvestPage={this.investPage.bind(this)}
                        toHomePage={this.homePage.bind(this)}
                        toMyPage={this.myPage.bind(this)}
                    >
                    </Title>
                </div>
                <ul className="table_title">
                    <li>
                        <Picker
                            data={this.state.data}
                            cols={this.state.cols}
                            value={this.state.asyncValue}
                            extra=""
                            onPickerChange={this.onPickerChange.bind(this)}
                        >
                            <List.Item  onClick={this.onClick.bind(this)}>
                            </List.Item>
                        </Picker>
                    </li>
                    <li>
                        <Picker
                            data={this.state.incomeData}
                            cols={this.state.cols}
                            value={this.state.incomeAsyncValue}
                            extra=""
                            onPickerChange={this.incomeOnPickerChange.bind(this)}
                        >
                            <List.Item  onClick={this.incomeOnClick.bind(this)}>
                            </List.Item>
                        </Picker>
                    </li>
                    <li>
                        <Picker
                            data={this.state.timeData}
                            cols={this.state.cols}
                            value={this.state.timeAsyncValue}
                            extra=""
                            onPickerChange={this.timeOnPickerChange.bind(this)}
                        >
                            <List.Item  onClick={this.timeOnClick.bind(this)}>
                            </List.Item>
                        </Picker>
                    </li>
                </ul>
                <div className="text_content">{this.state.text}</div>
            </div>

        )
    }
};

//
export default Invest;



